import React, { Component, Fragment } from 'react';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getCourses } from 'services/course';
import TraintypeInput from './TraintypeInput';
import FacultyInput from './FacultyInput';
import CourseInput from './CourseInput';
import StudyfieldInput from './StudyfieldInput';
import ClassesInput from './ClassInput';

export default class FilterPathEducate extends Component {
  state = { trainTypes: [], facultys: [], courses: [], studyFields: [] };

  componentWillMount = () => {
    // this.getTrainTypes();
  };

  getTrainTypes = async () => {
    const res = await getTrainTypes();
    this.setState({ trainTypes: res.data.items });
  };

  getFacultys = async () => {
    const res = await getFacultys();
    this.setState({ facultys: res.data.items });
  };

  getCourses = async () => {
    const res = await getCourses();
    this.setState({ courses: res.data.items });
  };

  getStudyFields = async () => {
    const res = await getCourses();
    this.setState({ studyFields: res.data.items });
  };

  handleChangeTrainType = () => {
    this.getFacultys();
  };

  handleChangeFaculty = () => {
    this.getCourses();
  };

  handleChangeCourse = () => {
    this.getStudyFields();
  };

  render() {
    const { form, traintype, faculty, course, studyfield, classes } = this.props;
    const { trainTypes, facultys, courses, studyFields } = this.state;
    return (
      <Fragment>
        <TraintypeInput
          form={form}
          trainTypes={trainTypes}
          onChange={this.handleChangeTrainType}
          {...traintype}
        />
        <FacultyInput
          form={form}
          facultys={facultys}
          onChange={this.handleChangeFaculty}
          {...faculty}
        />
        <CourseInput form={form} courses={courses} onChange={this.handleChangeCourse} {...course} />
        <StudyfieldInput form={form} studyFields={studyFields} {...studyfield} />
        <ClassesInput form={form} {...classes} />
      </Fragment>
    );
  }
}



