import React, { Component, Fragment } from 'react';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import Timeline from 'react-calendar-timeline';
import 'react-calendar-timeline/lib/Timeline.css';
import moment from 'moment';

const groups = [{ id: 1, title: 'SCOT 1' }, { id: 2, title: 'SCOT 2' }];

const items = [
  {
    id: 1,
    group: 1,
    title: 'Nghĩ tết',
    start_time: moment(),
    end_time: moment().add(1, 'day'),
    canMove: true,
    canResize: true,
    timeSteps: {
      day: 1,
    },
  },
  {
    id: 2,
    group: 2,
    title: 'Học quân sự',
    start_time: moment().add(-1, 'day'),
    end_time: moment().add(1, 'day'),
    canMove: true,
    canResize: true,
  },
  {
    id: 3,
    group: 1,
    title: 'Thi',
    start_time: moment().add(2, 'day'),
    end_time: moment().add(3, 'day'),
    canMove: true,
    canResize: true,
  },
];
export default class TimelineDemo extends Component {
  state = {};

  render() {
    return (
      <Row>
        <Col span={24}>
          <Timeline
            groups={groups}
            items={items}
            defaultTimeStart={moment().add(-12, 'day')}
            defaultTimeEnd={moment().add(12, 'day')}
          />
        </Col>
      </Row>
    );
  }
}



