import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Modal } from 'antd';
import { unitKnowledge } from 'dump/data';
import TimelineDemo from './Timeline';
import FormSearch from './FormSearch';
import DataSheet from './DataSheet';
import FormCreateNew from './FormCreateNew';
import NotePlan from './NotePlan';

class WholeSchoolTrainingPlanPage extends Component {
  state = { visible: false, visibleSelectPlan: false };
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  render() {
    return (
      <Card title="Kế hoạch đào tạo toàn trường">
        <BtnAddNew onClick={this.showModal} />
        <FormSearch />
        <DataSheet />
        <NotePlan />
        {/* <TimelineDemo /> */}
        <Modal
          title="Tạo kế hoạch mới"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          <FormCreateNew />
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <WholeSchoolTrainingPlanPage />
  </Page>
);



