import React, { Component } from 'react';
import { Form, Select, Col } from 'antd';
import { typeOfTraining } from 'dump/data';

const { Option } = Select;

// eslint-disable-next-line react/prefer-stateless-function
export default class TraintypeInput extends Component {
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Col span={6}>
        <Form.Item label="Hệ">
          {getFieldDecorator('traintype', {})(
            <Select>
              {typeOfTraining.map(item => (
                <Option key={item.code} value={item.code}>
                  {item.name}
                </Option>
              ))}
            </Select>
          )}
        </Form.Item>
      </Col>
    );
  }
}



