import React, { Component } from 'react';
import { Form, Select, Col } from 'antd';
import { classes } from 'dump/data';

const { Option } = Select;
// eslint-disable-next-line react/prefer-stateless-function
export default class ClassInput extends Component {
  render() {
    const { getFieldDecorator } = this.props.form;
    const { isShow, multiple } = this.props;
    return isShow ? (
      <Col span={6}>
        <Form.Item label="Lớp">
          {getFieldDecorator('class', {})(
            <Select mode={multiple ? 'multiple' : ''}>
              {classes.map(item => (
                <Option key={item.code} value={item.code}>
                  {item.name}
                </Option>
              ))}
            </Select>
          )}
        </Form.Item>
      </Col>
    ) : null;
  }
}

ClassInput.defaultProps = {
  isShow: true,
  multiple: false,
};



